const TOOLTIP_TEST_TIMEOUT = 3000;
const TOOLTIP_TEXT = 'Readonly rating gauge';
const DATATIP_TEXT = '3 star(s)';

describe('Test Rating component', () => {
  it('render', async function () {
    const content = render(<RatingGauge value={2} accessibleLabel="ariaText" />);
    const ariaText = await content.getAllByLabelText('ariaText');
    expect(ariaText).not.null;
  });

  it('applies Linaria CSS classes', async () => {
    const locale = matchTranslationBundle(['en'], new Set(['en-US', 'en']));
    const { default: translations } = await import(`../../resources/nls/${locale}/bundle.ts`);
    const env: Partial<RootEnvironment> = {
      translations: { '@oracle/oraclejet-preact': translations }
    };
    const content = render(
      <RootEnvironmentProvider environment={env}>
        <RatingGauge value={2} />
      </RootEnvironmentProvider>
    );
    const ratingGaugeElement = content.container.firstElementChild as HTMLElement;
    const classList = ratingGaugeElement.classList;
    expect(classList.length).equals(2);
  });
});

describe('Classlist test the various states Rating Gauge', () => {
  it('Test readonly Rating Gauge', async function () {
    const content = render(<RatingGauge value={2} isReadonly={true} />);
    const ratingGaugeElement = content.container.firstElementChild as HTMLElement;
    const classList = ratingGaugeElement.classList;
    expect(classList.length).equals(1);
    expect(ratingGaugeElement.getAttribute('aria-readonly')).equals('true');
    expect(ratingGaugeElement.getAttribute('aria-disabled')).null;
  });
  it('Test disabled Rating Gauge', async function () {
    const content = render(<RatingGauge value={2} isDisabled={true} />);
    const ratingGaugeElement = content.container.firstElementChild as HTMLElement;
    const classList = ratingGaugeElement.classList;
    expect(classList.length).equals(1);
    expect(ratingGaugeElement.getAttribute('aria-readonly')).null;
    expect(ratingGaugeElement.getAttribute('aria-disabled')).equals('true');
  });
  it('Test editable Rating Gauge', async function () {
    const content = render(<RatingGauge value={3} />);
    const ratingGaugeElement = content.container.firstElementChild as HTMLElement;
    const classList = ratingGaugeElement.classList;
    expect(classList.length).equals(2);
    expect(ratingGaugeElement.getAttribute('aria-readonly')).null;
    expect(ratingGaugeElement.getAttribute('aria-disabled')).null;
  });
  it('Test readonly and disabled both of Rating Gauge', async () => {
    const content = render(<RatingGauge value={3} isDisabled={true} isReadonly={true} />);
    const ratingGaugeElement = content.container.firstElementChild as HTMLElement;
    expect(ratingGaugeElement.getAttribute('aria-disabled')).equals('true');
    expect(ratingGaugeElement.getAttribute('aria-readonly')).null;
  });
});

describe('Test Rating Gauge ARIA props', () => {
  it('Default value verification using ARIA attributes', async () => {
    const content = render(<RatingGauge />);
    const ratingGaugeElement = content.container.firstElementChild as HTMLElement;
    expect(ratingGaugeElement.getAttribute('aria-valuemin')).equals('0');
    expect(ratingGaugeElement.getAttribute('aria-valuemax')).equals('5');
    expect(ratingGaugeElement.getAttribute('aria-valuenow')).equals('0');
  });
  it('Test value and max property of Rating Gauge', async function () {
    const content = render(<RatingGauge value={3} max={5} />);
    const ratingGaugeElement = content.container.firstElementChild as HTMLElement;
    expect(ratingGaugeElement.getAttribute('aria-valuemax')).equals('5');
    expect(ratingGaugeElement.getAttribute('aria-valuenow')).equals('3');
    expect(ratingGaugeElement.getAttribute('aria-valuemin')).equals('0');
    expect(ratingGaugeElement.getAttribute('aria-valuetext')).equals('3');
    expect(ratingGaugeElement.getAttribute('role')).equals('slider');
    expect(ratingGaugeElement.getAttribute('aria-labelledby')).null;
    //expect(ratingGaugeElement.getAttribute('aria-describedby')).null; TO-DO : https://jira.oraclecorp.com/jira/browse/JET-52220
  });
  it('Test tooltip as aria label of Rating Gauge', async () => {
    const content = render(<RatingGauge value={3} isReadonly={true} tooltip={TOOLTIP_TEXT} />);
    const ratingGaugeElement = content.container.firstElementChild as HTMLElement;
    expect(ratingGaugeElement.getAttribute('aria-describedby')).not.null;
    expect(ratingGaugeElement.getAttribute('aria-label')).equals(TOOLTIP_TEXT);
  });
  it('Test accesssibleLabel of Rating Gauge', async () => {
    const testAccessibleLabel = 'TEST-ACCESSIBLE-LABEL';
    const content = render(<RatingGauge value={2} accessibleLabel={testAccessibleLabel} />);
    const ratingGaugeElement = content.container.firstElementChild as HTMLElement;
    expect(ratingGaugeElement.getAttribute('aria-label')).equals(testAccessibleLabel);
  });
});

describe('Test describedby and labelledby of Rating Gauge', () => {
  it('Test describedby of Rating Gauge', async function () {
    const testAriaDescribedBy = 'TEST ARIA-DESCRIBED-BY';
    const content = render(<RatingGauge value={3} ariaDescribedBy={testAriaDescribedBy} />);
    const ratingGaugeElement = content.container.firstElementChild as HTMLElement;
    expect(ratingGaugeElement.getAttribute('aria-describedby')).equals(testAriaDescribedBy);
  });
  it('Test labelledby of Rating Gauge', async function () {
    const testAriaLabeledBy = 'TEST ARIA-LABELLED-BY';
    const content = render(<RatingGauge value={3} ariaLabelledBy={testAriaLabeledBy} />);
    const ratingGaugeElement = content.container.firstElementChild as HTMLElement;
    expect(ratingGaugeElement.getAttribute('aria-labelledby')).equals(testAriaLabeledBy);
  });
  it('Test aria-describedby concatenation for tooltip', async function () {
    const testAriaDescribedBy = 'TEST ARIA-DESCRIBED-BY';
    const testTooltip = 'TEST-TOOLTIP';
    const content = render(
      <RatingGauge
        value={3}
        ariaDescribedBy={testAriaDescribedBy}
        isReadonly={true}
        tooltip={testTooltip}
      />
    );
    const ratingGaugeElement = content.container.firstElementChild as HTMLElement;
    const ratingDescribedBy = ratingGaugeElement.getAttribute('aria-describedby');
    const describedByIds = ratingDescribedBy?.split(' ');
    expect(describedByIds?.length).equals(3);
    expect(describedByIds ? describedByIds[0] : '').equals('TEST');
    expect(describedByIds ? describedByIds[1] : '').equals('ARIA-DESCRIBED-BY');
  });
  it('Test aria-describedby concatenation for datatip', async function () {
    const testAriaDescribedBy = 'TEST ARIA-DESCRIBED-BY';
    const testDatatip = 'TEST-DATATIP';
    const content = render(
      <RatingGauge value={3} ariaDescribedBy={testAriaDescribedBy} datatip={testDatatip} />
    );
    const ratingGaugeElement = content.container.firstElementChild as HTMLElement;
    const ratingDescribedBy = ratingGaugeElement.getAttribute('aria-describedby');
    const describedByIds = ratingDescribedBy?.split(' ');
    expect(describedByIds?.length).equals(3); // including aria describedby for datatip
    expect(describedByIds ? describedByIds[0] : '').equals('TEST');
    expect(describedByIds ? describedByIds[1] : '').equals('ARIA-DESCRIBED-BY');
  });
});

