describe('Mouse Interaction', () => {
  const clientHints = getClientHints();
  if (typeof jest === 'undefined' && clientHints.browser === 'chrome') {
    mocha.timeout(TOOLTIP_TEST_TIMEOUT);
    it(
      'check toooltip correctly triggered with mouse interaction',
      async () => {
        const content = render(
          <RootEnvironmentProvider>
            <RatingGauge value={3} isReadonly={true} tooltip={TOOLTIP_TEXT} />
          </RootEnvironmentProvider>
        );
        const ratingGaugeElement = content.container.firstElementChild as HTMLElement;
        userEvent.hover(ratingGaugeElement);
        await waitFor(() => checkTooltipDatatipText(TOOLTIP_TEXT), {
          timeout: TOOLTIP_TEST_TIMEOUT
        });
      },
      TOOLTIP_TEST_TIMEOUT
    );
    it(
      'check datatip correctly triggered with mouse interaction',
      async () => {
        const content = render(
          <RootEnvironmentProvider>
            <RatingGauge value={3} datatip={DATATIP_TEXT} />
          </RootEnvironmentProvider>
        );
        const ratingGaugeElement = content.container.firstElementChild as HTMLElement;
        userEvent.hover(ratingGaugeElement);
        await waitFor(() => checkTooltipDatatipText(DATATIP_TEXT), {
          timeout: TOOLTIP_TEST_TIMEOUT
        });
      },
      TOOLTIP_TEST_TIMEOUT
    );
    it('check onCommit event is correctly triggered with mouse interaction', async () => {
      const clickListenerObj = { clicked: false };
      const handleOnCommit = (detail: { value: number | null }) => {
        if (detail.value != null && detail.value == 3) clickListenerObj.clicked = true;
      };
      const content = render(
        <RootEnvironmentProvider>
          <RatingGauge onCommit={handleOnCommit} />
        </RootEnvironmentProvider>
      );
      const thirdStarElement = content.container.firstElementChild?.children.item(2) as HTMLElement;
      const clientX = getClientX(thirdStarElement);
      userEvent.click(thirdStarElement, { clientX: clientX });
      await waitFor(() => expect(clickListenerObj.clicked).equals(true));
    });
    it('check onInput event is correctly triggered with mouse interaction', async () => {
      const hoverListenerObj = { hovered: false };
      const handleOnInput = (detail: any) => {
        if (detail.value != null && detail.value == 3) hoverListenerObj.hovered = true;
      };
      const content = render(
        <RootEnvironmentProvider>
          <RatingGauge onInput={handleOnInput} />
        </RootEnvironmentProvider>
      );
      const thirdStarElement = content.container.firstElementChild?.children.item(2) as HTMLElement;
      const clientX = getClientX(thirdStarElement);
      userEvent.hover(thirdStarElement, { clientX: clientX });
      await waitFor(() => expect(hoverListenerObj.hovered).equals(true));
    });
  }
});

