describe('Test "thresholds" property of Rating Gauge ', () => {
  if (typeof jest === 'undefined') {
    const thresholdValues = [
      { max: 1, color: 'danger', accessibleLabel: 'Poor' },
      { max: 2, accessibleLabel: 'Needs Improvement' },
      { max: 3, color: 'warning', accessibleLabel: 'Satisfactory' },
      { max: 4, color: 'success', accessibleLabel: 'Exceeds Expectations' },
      { max: 5, color: '#508223', accessibleLabel: 'Outstanding' }
    ];
    it('Test thresholds danger color', async function () {
      const content = render(<RatingGauge value={1} thresholds={thresholdValues} />);
      const ratingGaugeElement = content.container.firstElementChild?.firstElementChild
        ?.firstElementChild as HTMLElement;
      expect(ratingGaugeElement.classList.length).equals(2);
      expect(ratingGaugeElement.classList.contains('oj-c-rating-gauge-danger')).equals(true);
    });

    it('Test thresholds no color', async function () {
      const content = render(<RatingGauge value={2} thresholds={thresholdValues} />);
      const ratingGaugeElement = content.container.firstElementChild?.firstElementChild
        ?.firstElementChild as HTMLElement;
      expect(ratingGaugeElement.classList.length).equals(2);
      expect(ratingGaugeElement.classList.contains('oj-c-rating-gauge-neutral')).equals(true);
    });

    it('Test thresholds warning color', async function () {
      const content = render(<RatingGauge value={3} thresholds={thresholdValues} />);
      const ratingGaugeElement = content.container.firstElementChild?.firstElementChild
        ?.firstElementChild as HTMLElement;
      expect(ratingGaugeElement.classList.length).equals(2);
      expect(ratingGaugeElement.classList.contains('oj-c-rating-gauge-warning')).equals(true);
    });

    it('Test thresholds success color', async function () {
      const content = render(<RatingGauge value={4} thresholds={thresholdValues} />);
      const ratingGaugeElement = content.container.firstElementChild?.firstElementChild
        ?.firstElementChild as HTMLElement;
      expect(ratingGaugeElement.classList.length).equals(2);
      expect(ratingGaugeElement.classList.contains('oj-c-rating-gauge-success')).equals(true);
    });

    it('Test thresholds custom color', async function () {
      const CUSTOM_COLOR = '#508223';
      const content = render(<RatingGauge value={5} thresholds={thresholdValues} />);
      const ratingGaugeElement = content.container.firstElementChild?.firstElementChild
        ?.firstElementChild as HTMLElement;
      expect(ratingGaugeElement.classList.length).equals(1);
      expect(ratingGaugeElement.style.cssText.split(CUSTOM_COLOR).length).equals(4);
    });
  }
});
const checkTooltipDatatipText = (content: string) => {
  const targetElements = document.querySelectorAll('[role="tooltip"]');
  const targetElementItem = targetElements.item(0) as HTMLElement;
  const tooltipContainer = targetElementItem?.firstElementChild as HTMLElement;
  expect(tooltipContainer?.style.opacity).not.equals('');
  expect(tooltipContainer?.style.opacity).not.equals('0');
  expect(tooltipContainer?.style.maxHeight).not.equals('');
  expect(tooltipContainer?.style.maxHeight).not.equals('0');
  const innerHTML = tooltipContainer?.firstElementChild?.innerHTML;
  expect(innerHTML).equals(content);
};
const getClientX = (element: HTMLElement) => {
  const dim = element.getBoundingClientRect();
  const clientX = dim.x + dim.width / 2;
  return clientX;
};
